import { mapState, mapMutations } from 'vuex';
export default {
	computed: {
		...mapState(['hasLogin', 'userId'])
	},
	data() {
		return {
			amount: 0,
			paymentOrder: '',
			payPassword: '',
			payment_password: ''
		};
	},
	onLoad(e) {
		this.amount = e.amount;
		console.log(this.amount);
	},
	methods: {
		pass() {
			let that = this;
			uni.getStorage({
				key: 'payment_password',
				success: function(res) {
					that.payment_password = res.data;
				}
			});
			console.log(that);
			uni.getStorage({
				key: 'userId',
				success: function(res) {
					that.userId = res.data;
					console.log(res.data);
				}
			});
			uni.getStorage({
				key: 'payment_order',
				success: function(res) {
					that.paymentOrder = res.data;
					console.log(res.data);
				}
			});
			console.log(this.paymentOrder);
			if (this.payPassword == this.payment_password) {
				uni.showLoading({
					title: '支付中...'
				});

				setTimeout(() => {
					uni.hideLoading();
					setTimeout(() => {
						uni.getStorage({
							key: 'order_ids',
							success: function(res) {
								setTimeout(() => {
									uni.request({
										url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/pay/orders',
										method: 'PUT',
										data: {
											ids: res.data,
											totalMoney: that.amount,
											userId: that.userId
										},
										success: res => {
											console.log(res.data.msg);
											let isfull = res.data.msg;
											if (isfull) {
												uni.showToast({
													icon: null,
													title: '支付成功'
												});
												setTimeout(()=>{uni.hideToast();},500)
												setTimeout(() => {
													uni.redirectTo({
														url: '../success/success?amount=' + that.amount
													});
												}, 200);
											} else {
												setTimeout(() => {
													uni.showToast({
														icon: null,
														title: '余额不足'
													});
												}, 800);
												uni.hideToast();
												uni.navigateBack({
													delta: 1
												});
											}
										},
										fail: () => {},
										complete: () => {}
									});
								}, 500);
							}
						});
					}, 200);
				}, 700);
			} else {
				uni.showToast({
					icon: null,
					title: '密码错误'
				});
				setTimeout(()=>{uni.hideToast();},500)
			}
		}
	}
};
